







import { defineComponent, computed } from '@vue/composition-api';

export default defineComponent({
	props: {
		info: {
			type: Boolean,
			default: false,
		},
		success: {
			type: Boolean,
			default: false,
		},
		warning: {
			type: Boolean,
			default: false,
		},
		danger: {
			type: Boolean,
			default: false,
		},
		icon: {
			type: [String, Boolean],
			default: null,
		},
		center: {
			type: Boolean,
			default: false,
		},
	},
	setup(props) {
		const iconName = computed(() => {
			if (props.icon !== false && typeof props.icon === 'string') {
				return props.icon;
			}

			if (props.info) {
				return 'info';
			} else if (props.success) {
				return 'check_circle';
			} else if (props.warning) {
				return 'warning';
			} else if (props.danger) {
				return 'error';
			} else {
				return 'info';
			}
		});

		const className = computed<string | null>(() => {
			if (props.info) {
				return 'info';
			} else if (props.success) {
				return 'success';
			} else if (props.warning) {
				return 'warning';
			} else if (props.danger) {
				return 'danger';
			} else {
				return null;
			}
		});

		return { iconName, className };
	},
});
